.right .ant-card {
  margin-bottom: 20px ;
}
.right .ant-card .ant-card-cover {
  height: 200px;
  overflow: hidden;
  padding: 0px;
}
.right .ant-card .ant-card-cover img {
  margin-top: -100px;
}
.right .ant-card .right_box {
  width: 45%;
  height: 45%;
  overflow: hidden;
  position: relative;
  border-radius: 50%;
  margin-top: -120px;
}
.right .ant-card .right_box img {
  width: 100%;
  height: 100%;
}
.right .right_follow h2 {
  font-weight: normal;
  font-size: 18px;
}
.right .right_follow .ant-card-meta-description {
  text-align: center;
  font-size: 27px;
  color: #333333;
}
.right .right_follow .right_icon .ant-card-meta-description {
  text-align: left;
}
.right .right_follow ul {
  list-style-type: disc;
  list-style-position: inside;
}
.right .right_follow ul li {
  font-size: 14px;
  text-align: left;
}
