html {
  height: 100%;
}
html body {
  height: 100%;
}
html body #root {
  height: 100%;
}
html body #root .App {
  width: 100%;
  height: 100%;
}
html body #root .App .bg {
  width: 100%;
  height: 100%;
  background: url("../img/bg.jpg");
  position: fixed;
}
html body #root .App .head {
  height: 80px;
  line-height: 60px;
  text-align: center;
}
html body #root .App .head a {
  color: white;
  font-size: 17px;
}
html body #root .App .container .ant-row {
  margin: 0px !important;
}
html body #root .App .Footer {
  margin-top: 20px;
  background: black;
  position: relative;
  z-index: 99;
  color: white;
  text-align: center;
  font-size: 16px;
  line-height: 60px;
}
