body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

html,
body {
  font: 14px/1.5 Helvetica, sans-serif, STHeiTi;
  color: #333;
  outline: 0;
  margin: 0px;
}
/* 公用样式 */
ul {
  margin: 0px;
  padding: 0px;
  list-style: none;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0px;
  padding: 0px;
  font-weight: normal;
}
a {
  text-decoration: none;
}
a:focus {
  outline: none;
}
img {
  border: none;
  vertical-align: top;
}
/* 兼容ie7及以下 */
.clearfix {
  *zoom: 1;
}
.clearfix::after {
  content: '';
  display: block;
  clear: both;
}
input {
  text-indent: 5px;
  /* outline:none; */
}

html {
  height: 100%;
}
html body {
  height: 100%;
}
html body #root {
  height: 100%;
}
html body #root .App {
  width: 100%;
  height: 100%;
}
html body #root .App .bg {
  width: 100%;
  height: 100%;
  background: url(/Blog-rba/static/media/bg.57714370.jpg);
  position: fixed;
}
html body #root .App .head {
  height: 80px;
  line-height: 60px;
  text-align: center;
}
html body #root .App .head a {
  color: white;
  font-size: 17px;
}
html body #root .App .container .ant-row {
  margin: 0px !important;
}
html body #root .App .Footer {
  margin-top: 20px;
  background: black;
  position: relative;
  z-index: 99;
  color: white;
  text-align: center;
  font-size: 16px;
  line-height: 60px;
}

.host .ant-card {
  margin-bottom: 20px;
}
.host .ant-card .host_content .ant-card-meta-description {
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
}

.detail {
  background: white;
  border-radius: 4px;
  padding: 20px;
}
.detail p {
  color: #555;
}
.detail h1 {
  font-weight: normal;
}
.detail h2 {
  font-weight: bold;
}
.detail .detail_marked pre {
  border: 1px solid #ddd;
  border-radius: 5px;
  background: #f8f8f8;
}

.right .ant-card {
  margin-bottom: 20px ;
}
.right .ant-card .ant-card-cover {
  height: 200px;
  overflow: hidden;
  padding: 0px;
}
.right .ant-card .ant-card-cover img {
  margin-top: -100px;
}
.right .ant-card .right_box {
  width: 45%;
  height: 45%;
  overflow: hidden;
  position: relative;
  border-radius: 50%;
  margin-top: -120px;
}
.right .ant-card .right_box img {
  width: 100%;
  height: 100%;
}
.right .right_follow h2 {
  font-weight: normal;
  font-size: 18px;
}
.right .right_follow .ant-card-meta-description {
  text-align: center;
  font-size: 27px;
  color: #333333;
}
.right .right_follow .right_icon .ant-card-meta-description {
  text-align: left;
}
.right .right_follow ul {
  list-style-type: disc;
  list-style-position: inside;
}
.right .right_follow ul li {
  font-size: 14px;
  text-align: left;
}

